import React from 'react';
import { Link, Redirect } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import { Button } from '@ya.praktikum/react-developer-burger-ui-components';
import { YandexEmailInput } from 'yandex/yandex-email-input';
import {forgotPasswordAction} from 'services/actions/actions';

import styles from './page-forgot-password.module.css';

function PageForgotPassword () {
    const [isResettingPassword, isLoggedIn] = useSelector(store => [store.isResettingPassword, store.isLoggedIn]);
    const [email, setEmail] = React.useState('')

    const onChange = e => {
        setEmail(e.target.value);
    };

    const dispatch = useDispatch();
    const recoverPassword = (event) => {
        event.preventDefault();
        dispatch(forgotPasswordAction (email));
    };

    if(isLoggedIn) {
        return (
            <Redirect to="/" />
        )
    }

    if(isResettingPassword) {
        return (
            <Redirect to="/reset-password" />
        )
    }

    return (
        <section className={styles.body}>
            <form className={styles.form}
                onSubmit={recoverPassword}>
                <div className={styles.inputsContainer}>
                    <h1 className={styles.title}>
                        <p className="text text_type_main-medium"> </p>
                    </h1>
                    <div className={styles.input}>
                    <YandexEmailInput placeholder=" e-mail" value={email} name="email" onChange={onChange} size='default' />
                    </div>

                    <div className={styles.button}>
                        <Button
                            type="primary" 
                            size="medium" 
                            style={{ height: '56px' }} 
                            className="ml-1 mr-1 mb-1 mt-6">
                            
                        </Button>
                    </div>
                </div>
                
                <div className={styles.linksContainer}>
                    <p className="text text_type_main-default text_color_inactive"> ? 
                        <Link to='/login' className={styles.link}>
                        
                        </Link>
                    </p>
                </div>
            </form>
        </section>
    )
}

export default PageForgotPassword;